<template>
  <div>
    <i class="el-icon-back allback"  @click="$emit('back')"></i>
    <i v-if="isLogin" class="el-icon-delete allback deleteibtn" @click="delete1"></i>
    <div class="blogx" v-html="content"></div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    computed: {
      ...mapState(['isLogin'])  
    },
  data() {
    return {
      content: "<h1 style='text-align: center;'>博客详情页面</h1>"
    };
  },
  methods: {
    // 获取数据
    async getBlogx(id) {
      let { data: res } = await this.$http.get("/blogs/blogx/" + id);
      console.log(res);
      if (res.ok == 1) {
        this.content = res.data.content;
      }
    },
    async delete1() {
        let {data:res} = await this.$http.delete('/blogs/'+this.$route.params.id)
        console.log(res)
         if (res.ok == 0)
            return this.$notify.error({
              title: "删除失败",
              customClass: "alter"
            });
          this.$notify.success({
            title: "删除成功~",
            customClass: "alter"
          });
          this.$router.go("-1");
    }
  },
  created() {
    console.log(this.$route.params.id);
    this.getBlogx(this.$route.params.id);
  }
};
</script>

<style>
.deleteibtn {
  bottom: 180px !important;
}
.deleteibtn:hover {
  color: red !important;
}
</style>